import React from 'react';
import styled from 'styled-components';
import { BDARK, BSECUNDARY } from '../../../shared/colors';

const cSubmenu = ({className, items, ...props}) => {
    return(
        items.map((Item, index) => 
            <a className={className} key={index} href={Item.url} >{Item.title} </a>
        )
    );
}

const Submenu = styled(cSubmenu)`
    box-sizing: border-box;
    text-decoration: none;
    display: flex;
    font-weight: bold;
    background: ${BSECUNDARY};
    padding: .5em .5em .5em 2em;
    color: yellow; 

    &:hover{
        color: white;
        font-weight: normal;
        background: ${BDARK};
    }
`;

export default Submenu;


