import styled from 'styled-components';

import Logo from '../../assets/caraboboTeQuiero3.png'

export const Content = styled.div`
    background	: 	url(${Logo}) no-repeat bottom right;
	padding		:	1em;
	color		:	#333;
	display		:	block;
	width		:	${props => props.width ? props.width : '100%'};
	box-sizing  :	border-box;
	min-height	:	92vh;
`;
 


