import styled from 'styled-components';
import {BLIGHT, BDARK} from '../../../shared/colors';

export const Title = styled.span`
    padding: .5em;
    display: block;
    text-align: center;
    font-weight: bold;
    font-size: 1.4em;
    background: ${BDARK};
    color: yellow;
    height: 71.34px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
	cursor:	pointer;

`;

export const Enlace = styled.a`
    cursor: pointer;
	display: block;
	text-decoration: none;
	width: 100%;
    padding: .5em;
    color: ${BLIGHT};
	box-sizing: border-box;
    border-bottom: 2px solid rgba(0,0,0,.3);
    text-align: left;
    font-weight: bold;
    font-size: 1em;
`;

export const Button = styled.button`
    cursor: pointer;
	display: block;
	text-decoration: none;
	width: 100%;
    padding: .5em;
    background: rgba(255,255,255,0);
    color: ${BLIGHT};
	box-sizing: border-box;
    border: none;
    border-bottom: 2px solid rgba(0,0,0,.3);
    text-align: left;
    font-size: 1em;
    font-weight: bold;
    box-sizing: border-box;
`

export const Icon = Button.extend`
    flex: 0 1 1em;
`


