import React, { Component } from 'react';
import { injectGlobal } from 'styled-components';

import Sidebar from './ui/Sidebar/Sidebar';
import Wrapper from './ui/common/Wrapper';
import HeaderApp from './ui/HeaderApp';
import {Content} from './ui/Content';

injectGlobal`
  body {
  }
`;

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      visible: false,
    }
    this.onVisibleClick.bind(this);
  }

  onVisibleClick = () => {
    const newValue = {...this.state}
    this.setState({visible: !newValue.visible})
  }

  render() {
    return (
      <Wrapper container row >
        {this.state.visible && <Sidebar />}
        <Wrapper container column>
          <HeaderApp sidebar={this.onVisibleClick} visibleSidebar={this.state.visible}/>
          <Content />
        </Wrapper>
      </Wrapper>
    );
  }

}

export default App;



