// Import Dependencies
import React from 'react';
import styled from 'styled-components';
import {BPRIMARY, BSECUNDARY} from '../shared/colors';

// Import Components
import Bars from 'react-icons/lib/fa/bars';
import Wrapper from './common/Wrapper'

const sBar = {
	fontSize: "2em",
	textAlign: "center",
}

const Button = styled.button`
	background: none;
	border: none;
	color: #fff;
	cursor: pointer;
	
	&:hover {
		background: ${props => !props.colapse ? BSECUNDARY : BPRIMARY };
		border-radius: 50%;
		padding: .5em;
		color: yellow;
	}
`

const Header = ({visibleSidebar, ...props}) => (
	<Wrapper container row height='71.34px' primary={!visibleSidebar} dark={visibleSidebar}>
		<Wrapper left flex="0 1 2em" alignCenter Center>
			<Button onClick={props.sidebar} colapse={visibleSidebar}><Bars style={sBar}/> </Button>
		</Wrapper>		
		<Wrapper row Center fontSize='1.2em' bold>
			TITULO DE LA APLICACION
		</Wrapper>
	</Wrapper>
);

export default Header;


