/******************************************
* Import Dependencies
******************************************/
import styled from 'styled-components';

/******************************************
* Import constantes
******************************************/
import {BPRIMARY, BSECUNDARY, BDARK, BLIGHT} from '../../shared/colors'

const Wrapper = styled.div`
	background-color:	${props => 
							(props.primary && BPRIMARY) || 
							(props.secundary && BSECUNDARY) || 
							(props.dark && BDARK) || 
							(props.light && BLIGHT) 
						};
	box-sizing: border-box;
	color			:	${props => props.light ? '#000' : '#FFF'};
	display: ${props => props.row || props.column || props.flex ? 'flex' : 'block' };
	flex-direction: ${props => (props.row && 'row') || (props.column && 'column')};
	font-weight: ${props=> props.bold ? 'bold' : 'normal'};
	height: ${props => (props.fullHeight && '100vh') || props.height };
	padding		:	${props => props.container ? 0 : '1em'};
	width:	${props => props.size ? props.size : '100%'};
	${props => (props.Center && "align-items: center")};
	${props => (props.pointer && "cursor: pointer")};
	${props => (props.flex) && "flex: "+props.flex};
	${props => (props.fontSize) && "font-size: "+props.fontSize};
	${props => (props.alignCenter) && "text-align: center"};
`;

export default Wrapper;


