import React, {Component} from 'react';
import { BPRIMARY, BDARK } from '../../shared/colors';

import Logo from '../../../assets/caraboboTeQuiero2.png'

import Bank from 'react-icons/lib/fa/bank';
import Ambulance from 'react-icons/lib/fa/ambulance';
import Lock from 'react-icons/lib/fa/lock';
import Seccion from './partials/Seccion';
import {Enlace, Title} from './partials/Enlace';
import Wrapper from '../common/Wrapper';

const fontSize = {
	fontSize: '1.5em',
	paddingRight: '.2em'
}

class Sidebar extends Component {
    constructor(props){
        super(props);
		this.state = {
			colapse: false,
			menu: [
				{
					title: "Menu 1",
					active: false,
					icon: <Ambulance style={fontSize}/>,
					submenu: [
						{
							title: "Item 1",
							url: "/item1"
						},
						{
							title: "Item 2",
							url: "/item2"
						}
					]
				},
				{
					title: "Menu 2",
					active: true,
					icon: <Bank style={fontSize} />,
					url: "/menu2"
				},
				{
					title: "Menu 3",
					active: false,
					icon: <Lock style={fontSize} />,
					submenu: [
						{
							title: "Item 1",
							url: "/menu2/item1"
						},
						{
							title: "Item 2",
							url: "/menu2/item2"
						},
						{
							title: "Item 3",
							url: "/menu2/item3"
						},
						{
							title: "Item 4",
							url: "/menu2/item4"
						},
						{
							title: "Item 5",
							url: "/menu2/item5"
						},
					]
				},				
			]
		}

		this.toggleColapse = this.toggleColapse.bind(this)
	}
	
	toggleColapse() {
		this.setState({colapse: !this.state.colapse});
	}

	activo(i) {
		const valor = !this.state.menu[i].active
		let newMenu = {...this.state.menu}
		newMenu[i].active=valor
		this.setState({menu: newMenu})
	}

    render(){
		const Degradado = `linear-gradient(to bottom, ${BDARK} 10% , ${BPRIMARY} 45%, ${BPRIMARY} 60%) `;
		console.info(Degradado);
		const Estilo = {
			width: this.state.colapse ? '5em' : '16em',
			height: '100vh',
			background: Degradado,
			display: 'flex',
			flexDirection: 'column',
			justifyContent: 'space-between'

		};
		const Imagen = {
			width: '100%',
			alignSelf: 'end'

		}
        return(
            <div className={this.props.className} style={Estilo}>
				<div>
					<Title >{!this.state.colapse ? <span onClick={this.toggleColapse}>Titulo del Menu</span> : <img onClick={this.toggleColapse} style={Imagen} src={Logo} alt="Logo Carabobo Te Quiero" />}</Title>
					{this.state.menu.map((Menu, i) =>
						(!Menu.submenu) ? <Enlace key={i} href={Menu.url}>{Menu.icon} {!this.state.colapse && Menu.title}</Enlace> : <Seccion key={i} submenu={Menu.submenu} mainTitle={Menu.title} icon={Menu.icon} colapse={this.state.colapse}/> 
					)}
				</div>
				{!this.state.colapse &&
					<Wrapper>
						<img style={Imagen} src={Logo} alt="Logo Carabobo Te Quiero" />
					</Wrapper>
				}
            </div>
        );
    }

}


export default Sidebar;


