import React, { Component } from 'react';
import Down from 'react-icons/lib/fa/chevron-down';
import Close from 'react-icons/lib/fa/close';

import Submenu from './Submenu';
import {Button, Icon} from './Enlace';
import { BLIGHT } from '../../../shared/colors';

const StyleWrapper = {
    display: 'flex',
    color: BLIGHT
}

class Seccion extends Component {
    constructor(props){
        super(props);
        this.state = {
            open: false
        }

        this.handlerClick = this.handlerClick.bind(this)
    }

    handlerClick() {
        const newOpen = this.state.open
        this.setState({open: !newOpen})
    }


    render(){
        return(
            <div>
                <div style={StyleWrapper}><Button onClick={this.handlerClick}>{this.props.icon}{!this.props.colapse && this.props.mainTitle}</Button><Icon onClick={this.handlerClick}>{this.state.open ? <Close /> : <Down />}</Icon></div>
                 {this.state.open && <Submenu items={this.props.submenu} />}
            </div>
        );
    }
    
}

export default Seccion;


